@charset "UTF-8";
*, ＊::before, ＊::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

mb-100 {
  margin-bottom: 100px; }

body {
  background: #abd4d5;
  font-family: "Noto Sans TC", serif; }

header {
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  header h1 {
    color: white;
    font-size: 3rem; }

form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  /* Chrome, Safari, Edge, Opera */
  /* Firefox */ }
  form input, form button {
    border: none;
    border-radius: 5px;
    padding: 0.25rem 0.5rem;
    font-size: 1.75rem;
    margin: 0.1rem; }
  form input[type="number"] {
    width: 50px;
    text-align: center; }
  form input::-webkit-outer-spin-button,
  form input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }
  form input[type=number] {
    -moz-appearance: textfield; }
  form button {
    background-color: #367c93;
    color: white;
    cursor: pointer;
    border-radius: 60%;
    padding: 10px 16px;
    font-size: 24px; }

section {
  display: flex;
  flex-direction: column;
  align-items: center; }
  section div.todo {
    width: 40vw;
    display: flex;
    align-items: center;
    margin: 0.2rem 0rem;
    transition: all 0.1s ease; }
    section div.todo p {
      background-color: white;
      color: black;
      padding: 0.25rem 0.5rem;
      font-size: 1.75rem; }
    section div.todo p.todo-text {
      width: 60%; }
    section div.todo p.todo-time {
      width: 20%;
      text-align: center; }
    section div.todo button {
      padding: 10px 16px;
      font-size: 24px;
      border: none;
      cursor: pointer;
      color: white; }
    section div.todo button.Complete {
      background-color: #1c8585;
      border-radius: 60%;
      margin-left: 15px;
      margin-right: 15px;
      padding: 10px 16px;
      font-size: 24px; }
  section button.Delete {
    background-color: #9ef5f2;
    border-radius: 60%; }

i {
  pointer-events: none; }

.done {
  opacity: 0.3;
  text-decoration: line-through;
  text-decoration-thickness: 0.2rem; }

@keyframes scaleUp {
  from {
    transform: scale(0); }
  to {
    transform: scale(1); } }

@keyframes scaleDown {
  to {
    transform: scale(0); } }

div.sort {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0rem; }
  div.sort button {
    background-color: #367c93;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    padding: 0.25rem 0.5rem;
    font-size: 1.75rem; }
